<template>
        <div class="newsMain">
                <div class="container">
                        <div class="pc_show">
                                <publicBreadcrumb :item="pageItem"></publicBreadcrumb>
                        </div>
                        <h1 class="newsTitle">{{ detailData?.title }}</h1>
                        <div class="subStr">
                                <p class="item">{{ detailData?.type }}</p>
                                <p class="item">
                                        <i class="iconfont icon-shijian"></i
                                        >{{ detailData?.timeStr }}
                                </p>
                        </div>
                        <div class="content" v-html="detailData?.content"></div>
                        <div class="btnGroup">
                                <button class="btn" @click="nuxtBack">
                                        {{ t('Battle.back') }}
                                </button>
                        </div>
                </div>
        </div>
</template>
<script setup>
import { getNewsDetail } from '@/api/news'
import { formatterTimeZone, nuxtBack } from '@/utils/tools'
import { useAppConfig } from 'nuxt/app'
const removeStr = (str, char) => {
        const index = str.indexOf(char)
        if (index !== -1) {
                return str.slice(0, index)
        }
        return str
}
const { t } = useI18n()
const hostUrl = useAppConfig().public.hostUrl
const detailData = ref({})
const route = useRoute()
const lang = route.query.key
const urlArr = route.fullPath.split('/').reverse()
urlArr[0] = removeStr(urlArr[0], '?')
// const paramsUrl = `https://test-academy.thetraderfunds.com/en/${urlArr[1]}/${urlArr[0]}`
const paramsUrl = `${hostUrl}/${lang}/${urlArr[1]}/${urlArr[0]}`
getNewsDetail({ url: paramsUrl }).then((res) => {
        if (res.data) {
                detailData.value = res.data
                detailData.value.timeStr = formatterTimeZone(res.data.releaseTime)
        }
})
const pageItem = ref([
        {
                path: '/academy',
                page: 'User.tips7',
        },
        {
                path: '/news',
                page: 'common.news',
        },
        {
                path: '',
                page: 'common.newsDetails',
        },
])
</script>
<style scoped lang="scss" src="../index.scss"></style>
